@import url(https://fonts.googleapis.com/css?family=Roboto:500);

.App {
  text-align: center;
  padding: 2% 2%;
  background-color: #8884d8;
}

.App * {
  box-sizing: border-box;
}

ul {
  padding: 0;
  list-style: none;
}

.number-events-input{
  margin-top: 10px;
}

p {
  margin: 5px 0;
}

.display-none {
  display: none;
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 10px;
  outline: none;
  border: 2px solid #5e58cb;
  font-size: 30px;
  line-height: 26px;
  align-self: center;
  height: 50px;
  text-align: center;
  width: 400px;
  background-color: white;
}

.city {
  width: 400px;
}

.CitySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 400px;
  max-width: 400px;
  margin: auto;
  position: relative;
}

.suggestions {
  width: 390px;
  z-index: 2;
  align-self: center;
  margin: 0;
  position: absolute;
  top: 2.1em;
  font-size: 26px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
  
}

.suggestions > li {
  text-align: left;
  padding: 8px;
  background-color: #eee;
  cursor: default;
}

.suggestions > li:last-child {
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
  cursor: pointer;
}

.event-list{
  display: grid;
  list-style: none;
  text-align: center;
  grid-template-columns: repeat(auto-fit, minmax(400px, 2fr));
}


.event {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 10px 35px;
  margin: 10px;
  text-align: left;
  border: 2px solid #5e58cb;
  border-radius: 4px;
  position: relative;
  background-color: white;
  border-radius: 20px;
  min-height: 400px;

} 

h3 {
  margin: 10px 0 10px 0; 
}

.meet-title{
  text-shadow: 4px 4px 2px #5e58cb;
}

.summary{
  text-shadow: 2px 2px 2px #adaae4;
  margin: 0.4em 0 0.1em 0;
}

.details-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  outline: none;
  cursor: pointer;
  border-radius: 10px;
}

.name {
  font-size: 18px;
  font-weight: 600;
}

.group-name {
  text-transform: uppercase;
  font-size: 14px;
}

.going,
.visibility {
  font-style: italic;
}

.description {
  overflow-x: hidden;
}

.numberOfEvents {
  width: 200px;
  margin: 20px auto;
  text-align: center;
  font-size: 22px;
}

.button-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  font-family: sans-serif;
}

.Alert {
  text-align: center;
  padding: 0;
  margin-top: 0;
  margin-bottom: 0;
  font-weight: bold;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}

.data-vis-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  background-color: white;
  border-radius: 20px;
  margin-bottom: 10px;
}
.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}
.recharts-surface {
  overflow: visible;
}

.login-button {
  color: #494949;
  text-transform: uppercase;
  text-decoration: none;
  background: #ffffff;
  padding: 20px;
  border: 4px solid #494949;
  display: inline-block;
  transition: all 0.4s ease 0s;
  min-width: 10rem;
  margin: 1rem;
}

.google-btn {
  width: 184px;
  height: 42px;
  margin: 1rem;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
  background: #1669f2;
}
.google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: -8px;
  width: 18px;
  height: 18px;
}
.btn-text {
  float: right;
  margin: 11px 11px 0 0;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
}

.data-vis-wrapper .recharts-responsive-container {
  min-width: 100%;
  display: flex;
}

@media only screen and (min-width: 992px) {
  .data-vis-wrapper {
    flex-direction: row;
  }
  .data-vis-wrapper .recharts-responsive-container {
    flex-basis: 50%;
    max-width: 50%;
    min-width: auto;
    margin: 0 auto;
  }

}